.NavbarItems{
    background:  linear-gradient(90deg, rgb(34, 87, 126) 0%, rgba(85, 132, 172,1)100%);
    height: 80px;
    display: flex;
    justify-content: left;
    align-items:  center;
    font-size: 1.4rem;
}
.nav-menu{
    display: grid;
    grid-template-columns: repeat(5,auto);
    grid-gap: 35px;
    list-style: none;
    padding: 5px;
}
.nav-links {
    color: white;
    padding: 10px;
    text-decoration: none;
}
.nav-links-active{
    color: white;
    padding: 10px;
    text-decoration: none;
    background-color: rgb(85, 132, 172);
    border-radius: 4px;
}

.navbar-logo{
    color: white;
    margin-right: 30px;
    margin-left: 30px;

}
.nav-links:hover {
    background-color: rgb(85, 132, 172);
    border-radius: 4px;
    transition: all 0.4s ease-out;
}
.fa-brands {
    color: #fff;
    margin-left: 30px;
}

